@charset "UTF-8";

@import url(../libs/normalize.css/normalize.css);

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/Montserrat-Light.woff2") format("woff2");
}

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2");
}

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2");
}

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2");
}

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2");
}

@font-face {
  font-family: "slick";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/slick.woff2") format("woff2");
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  position: relative;
  min-height: 100%;
  font-size: 16px;
}

body {
  font-family: 'Montserrat', Helvetica, Arial, sans-serif;
  width: 100%;
  min-height: 100%;
  margin: 0;
  padding: 0;
}

img,
embed,
object,
video {
  max-width: 100%;
}

svg {
  max-width: 100%;
  max-height: 100%;
}

p {
  font-weight: 400;
  color: #414141;
  line-height: 1.5;
  margin: .625rem 0;
}

a {
  text-decoration: none;
}

ul {
  list-style-type: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  font-weight: normal;
}

.wrapper {
  position: relative;
  min-height: 100%;
  overflow: hidden;
}

.container {
  position: relative;
  width: 100%;
  max-width: 1240px;
  margin: 0 auto;
}

.section {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 1.5625rem 0;
  background-color: #f7f7f7;
}

.title {
  font-weight: 600;
  font-size: 2rem;
  color: #414141;
  line-height: 1;
  margin-bottom: 1.5625rem;
}

/* Slider */

.slick-loading .slick-list {
  background: #fff url("../img/ajax-loader.gif") center center no-repeat;
}

/* Icons */

@font-face {
  font-family: "slick";
  src: url("../fonts/slick.eot");
  src: url("../fonts/slick.eot?#iefix") format("embedded-opentype"), url("../fonts/slick.woff2") format("woff2"), url("../fonts/slick.ttf") format("truetype"), url("../fonts/slick.svg#slick") format("svg");
  font-weight: normal;
  font-style: normal;
}

/* Arrows */

.slick-prev,
.slick-next {
  position: absolute;
  display: block;
  height: 20px;
  width: 20px;
  line-height: 0px;
  font-size: 0px;
  cursor: pointer;
  background: transparent;
  color: transparent;
  top: 50%;
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
  padding: 0;
  border: none;
  outline: none;
}

.slick-prev:hover,
.slick-prev:focus,
.slick-next:hover,
.slick-next:focus {
  outline: none;
  background: transparent;
  color: transparent;
}

.slick-prev:hover:before,
.slick-prev:focus:before,
.slick-next:hover:before,
.slick-next:focus:before {
  opacity: 1;
}

.slick-prev.slick-disabled:before,
.slick-next.slick-disabled:before {
  opacity: 0.25;
}

.slick-prev:before,
.slick-next:before {
  font-family: "slick";
  font-size: 20px;
  line-height: 1;
  color: black;
  opacity: 0.75;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-prev {
  left: -25px;
}

[dir="rtl"] .slick-prev {
  left: auto;
  right: -25px;
}

.slick-prev:before {
  content: "←";
}

[dir="rtl"] .slick-prev:before {
  content: "→";
}

.slick-next {
  right: -25px;
}

[dir="rtl"] .slick-next {
  left: -25px;
  right: auto;
}

.slick-next:before {
  content: "→";
}

[dir="rtl"] .slick-next:before {
  content: "←";
}

/* Dots */

.slick-dotted.slick-slider {
  margin-bottom: 0px;
}

.slick-dots {
  position: absolute;
  bottom: 15px;
  list-style: none;
  display: block;
  text-align: center;
  padding: 0;
  margin: 0;
  width: 100%;
}

.slick-dots li {
  position: relative;
  display: inline-block;
  height: 20px;
  width: 20px;
  padding: 0;
  cursor: pointer;
}

.slick-dots li button {
  border: 0;
  background: transparent;
  display: block;
  height: 20px;
  width: 20px;
  outline: none;
  line-height: 0px;
  font-size: 0;
  color: transparent;
  padding: 5px;
  cursor: pointer;
}

.slick-dots li button:hover,
.slick-dots li button:focus {
  outline: none;
}

.slick-dots li button:hover:before,
.slick-dots li button:focus:before {
  opacity: 1;
}

.slick-dots li button:before {
  position: absolute;
  top: 0;
  left: 0;
  content: "•";
  width: 20px;
  height: 20px;
  font-family: "slick";
  font-size: 6px;
  line-height: 20px;
  text-align: center;
  color: white;
  opacity: 0.25;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-dots li.slick-active button:before {
  color: white;
  opacity: 0.75;
}

/* Slider */

.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.slick-track:before,
.slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

.brands-grid {
  padding: 1rem 2rem;
}

.brands-grid__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.brands-grid__item {
  flex-basis: 12%;
}

.brands-grid__img {
  filter: grayscale(1);
}

.brands__list {
  display: flex;
  flex-wrap: wrap;
}

.brands__item {
  flex: 0 0 32%;
  margin-right: 0.78125rem;
  margin-bottom: 0.78125rem;
}

.brands__item:nth-child(3n) {
  margin-right: 0;
}

.brands__link {
  display: flex;
  width: 100%;
  height: 100%;
  position: relative;
}

.brands__link::after {
  content: '';
  position: absolute;
  display: flex;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
}

.brands__img {
  display: block;
  width: 100%;
  height: 100%;
}

.brands__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 2rem;
  color: #ffffff;
  z-index: 10;
}

.brands__title {
  font-weight: 400;
  font-size: 2rem;
}

.contacts__item {
  display: flex;
  align-items: center;
  margin-bottom: 0.78125rem;
}

.contacts__title {
  margin: 0 .5rem;
}

.contacts__icon {
  width: 13px;
  height: 13px;
  fill: #dadada;
}

.copyright {
  display: block;
  margin-top: 1.5625rem;
}

.footer-brands__item {
  margin-bottom: 0.78125rem;
}

.footer-brands__link {
  color: #dadada;
}

.footer-brands__link:hover {
  text-decoration: underline;
}

.footer-nav__item {
  margin-bottom: 0.78125rem;
}

.footer-nav__link {
  color: #dadada;
}

.footer-nav__link:hover {
  text-decoration: underline;
}

.footer-soc__item {
  display: flex;
  margin-bottom: 0.78125rem;
}

.footer-soc__link {
  display: flex;
  align-items: center;
  color: #dadada;
}

.footer-soc__link:hover {
  text-decoration: underline;
}

.footer-soc__icon {
  width: 13px;
  height: 13px;
  fill: #dadada;
  margin-right: .5rem;
}

.footer {
  font-size: .875rem;
  font-weight: 300;
  color: #dadada;
  padding: 3.125rem 0;
  background-color: #003533;
}

.footer__container {
  display: flex;
  justify-content: space-between;
}

.footer__title {
  font-size: 18px;
  font-weight: 500;
  margin-bottom: 1.5625rem;
}

.header {
  background-color: #003533;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1.5625rem 0;
}

.hero {
  padding: .9375rem 0;
}

.link {
  display: block;
  color: #003533;
}

.logo {
  display: flex;
}

.logo__icon {
  width: 11.75rem;
  height: 2.1875rem;
  fill: #ffffff;
}

.nav {
  display: flex;
}

.nav__list {
  display: flex;
}

.nav__item {
  margin-right: .9375rem;
}

.nav__link {
  font-size: 1rem;
  font-weight: 300;
  color: #ffffff;
}

.nav__link:hover {
  color: #f7f7f7;
}

.news__list {
  display: flex;
}

.news__item {
  display: flex;
  flex-direction: column;
  flex: 1;
  padding: .5rem;
}

.news__item:last-child {
  margin-right: 0;
}

.news__text {
  color: #414141;
}

.news__title {
  font-size: 1.125rem;
  font-weight: 500;
  margin-bottom: .9375rem;
}

.news__img {
  margin-bottom: .9375rem;
}

.page-header {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 21.875rem;
}

.page-header__wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.page-header__container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.page-header__title {
  font-size: 4rem;
  color: #ffffff;
  margin: 0;
}

.slider {
  max-height: 450px;
  margin: 0;
  overflow: hidden;
}

.socials {
  display: flex;
}

.socials__list {
  display: flex;
}

.socials__item {
  margin-right: .625rem;
  display: flex;
}

.socials__item:last-child {
  margin-right: 0;
}

.socials__link {
  display: flex;
}

.socials__icon {
  width: 1rem;
  height: 1rem;
  fill: #7c8391;
}

.socials__icon:hover {
  fill: #ffffff;
}

@media (max-width: 768px) {
  html {
    font-size: 14px;
  }
}

@media (max-width: 480px) {
  html {
    font-size: 13px;
  }
}