* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

html {
	position: relative;
	min-height: 100%;
	font-size: 16px;
	@include tablets {
		font-size: 14px;
	}
	@include phones {
		font-size: 13px;
	}
}

body {
	font-family: 'Montserrat', Helvetica, Arial, sans-serif;;
	width: 100%;
	min-height: 100%;
	margin: 0;
	padding: 0;
}

img, embed, object, video {
	max-width: 100%;
}

svg {
	max-width: 100%;
	max-height: 100%;
}

p {
	font-weight: 400;
	color: $dark;
	line-height: 1.5;
	margin: .625rem 0;
}

a {
	text-decoration: none;
}

ul {
	list-style-type: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  font-weight: normal;
}

.wrapper {
	position: relative;
	min-height: 100%;
	overflow: hidden;
}

.container {
	position: relative;
	width: 100%;
	max-width: 1240px;
	margin: 0 auto;
}

.section {
	display: flex;
	flex-direction: column;
	width: 100%;
	padding: $gutter 0;
	background-color: $gray;
}

.title {
	font-weight: 600;
	font-size: 2rem;
	color: $dark;
	line-height: 1;
	margin-bottom: 1.5625rem;
}